header {	
	padding: 0;
	/* position: fixed; */
	background: #000;
	right: 0;
	left: 0;    
	margin-bottom: 0;
	top: 0;
	z-index: 2;
}

.header-container {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	align-content: center;  
	position: relative;

}

.header-text-container {
	width: 95%;
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: center;
}

header .header-logo{
	padding-right: 1em;
}

header .header-logo img{
	display: flex;
}

nav.main {

}

nav.main ul {  
	padding: 0 0.5rem;
	margin: 0;
}

nav.main ul ul {
	display: none;
	position: absolute;
	top: 100%;
	left: -0.5rem;
	width: 10rem;
}

nav.main ul ul ul {
	top: -0.5rem;
	right: 100%;
	left: inherit;
}

nav.main ul li {
	display: block;
	list-style: none outside;
	position: relative;
}

nav.main > ul > li > ul li:not(:last-child) {
	/*padding-bottom: 0.5rem;*/
}

nav.main > ul > li {
	padding: 0 0.5rem 0 0;
}

nav.main ul li:hover > a {
}

nav.main ul > li > ul li a {
	width: 240px;
	background: rgba(0, 0, 0, 0.6);
	border-top: 1px solid #ffffff;
	display: flex;
	align-items: center;
	padding: 0.94em 0.6em;
}

nav.main ul li a {
	padding: 0.94em 0.6em;    
	text-decoration: none;
	color: #fff;
	text-transform: uppercase;
	font-weight: bold;
	display: flex;
	/*background: #333;
	border-radius: 0.4rem;
	padding: 0.84em 0.4em;
	align-items: center;*/

}

nav.main ul li a:hover,
nav.main ul li:hover > a {
	background: #f47624;
}

#main-nav-toggle,
#toggle {
	display: none;
	color: white;
}

#toggle {
	display: inline-block;  
	align-items: right;
	padding: 1rem;
	font-size: 30px;
	/*position: relative;
	flex: 1 0 100%;	*/
}

#toggle:hover {
	background: #f47624;
}

#toggle:before {
	/*display: block;
	content: '';
	position: relative;
	padding-right: 0.3rem;
	font-weight: bold;*/
}

form .search-form{
	position: relative;
	top: 50%;
	left: 50%;  
	width: 50px;
	height: 50px;
	background: white;
	box-sizing: border-box;
	border-radius: 25px;
	border: 4px solid white;
	padding: 5px;
	width: 5%;
}

input[type="search"]{
	position: absolute;
	top: 0;
	right: 0;
	width: 20%;
	height: 42.5px;
	line-height: 30px;
	outline: 0;
	border: 0;
	display: none;
	font-size: 1em;
	border-radius: 20px;
	padding: 0 3em 0 1em;

}

.fa-search{
	box-sizing: border-box;
	padding: 10px;
	width: 42.5px;
	height: 42.5px;
	position: absolute;
	top: 0;
	right: 0;
	border-radius: 50%;
	color: #ffffff;
	text-align: center;
	font-size: 1.2em;

}

.search-form:hover{  
	cursor: pointer;
}

.search-form:hover input{
	display: block;
}

.search-form:hover .fa-search{
	background: #f47624;
	color: white;
}

@media screen and (max-width: 800px) /* екран до 800px */{
	header{
		position: static;
	}

	#toggle {
		display: flex;

	}

	.header-text-container{
		justify-content: space-between;
	}

	nav.main {
		display: none;
		/*border-top: 2px solid #000;*/
	}

	nav.main ul li:not(:first-child) {
		/*padding-top: 0.5rem;*/
	}

	#main-nav-toggle:checked + header nav.main {
		display: block;
		flex: 1 0 100%;
	}

	#main-nav-toggle:checked + header #toggle:before {
		/*content: 'X';*/
	}

	.fa-search {
		display: none;
	}

	.header-container,
	.header-text-container {
		width: 100%;
	}

	nav.main ul,
	nav.main > ul > li,
	nav.main > ul > li > ul > li {
		padding: 0;
		margin: 0;
	}

	nav.main ul ul {
		display: block;
		position: static;
		width: 100%;
	}

	nav.main ul > li > ul li a {
		width: 100%;
		padding-left: 4.5em;
		text-transform: capitalize;
	}

	nav.main ul > li > ul li a{
		border-top: 1px solid #fe7800;
	}

	nav.main ul > li > a {
		border-top: 1px solid #fe7800;
		padding-left: 3em;
		text-transform: capitalize;
		/*font-size: 1.5em;*/
	}
}

@media screen and (min-width: 800px) /*екран над 800px */ {
	nav.main ul {
		display: flex;
	}

	#toggle {
		display: none;
	}

	nav.main ul li:hover > ul {
		display: block;
	}
}
