/*
 * https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */
@import url("https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700,700i|PT+Serif:400,400i,700,700i&subset=cyrillic,cyrillic-ext,latin-ext");
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/*
 * Simple white space reset
 */
* {
  padding: 0;
  margin: 0; }

html {
  font: 12px/1.5 PT Sans, Geneva, sans-serif; }

@media (min-width: 600px) {
  html {
    font-size: 12px; } }

@media (min-width: 1024px) {
  html {
    font-size: 14px; } }

@media (min-width: 1240px) {
  html {
    font-size: 16px; } }

h1, h2, h3, h4, h5, h6 {
  font: 1em/1.2 PT Sans, Geneva, sans-serif;
  margin: 0 0 0.5em 0;
  padding: 0; }

h1 {
  font-size: 2.2rem; }

h2 {
  font-size: 2.0rem; }

h3 {
  font-size: 1.8rem; }

h4 {
  font-size: 1.6rem; }

h5 {
  font-size: 1.4rem; }

h6 {
  font-size: 1.2rem; }

p {
  margin: 0;
  padding-bottom: 1.5rem; }

ol, ul, dl {
  margin: 0 0 1.5rem 0; }

ul, ol {
  padding: 0;
  margin-left: 1.5rem; }

ul li, ol li {
  list-style-position: inside; }

ul ul {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem; }

ul li {
  list-style-type: square; }

ul ul li {
  list-style-type: disc; }

ul ul ul li {
  list-style-type: circle; }

blockquote {
  border-left: 4px solid #DDD;
  font-style: italic;
  margin: 0 0 1.5rem 2rem;
  padding: 1rem 2rem;
  position: relative;
  color: #444;
  font-family: "Georgia", serif;
  padding-top: 0; }

blockquote .author {
  text-align: right; }

table {
  width: 100%;
  table-layout: fixed;
  margin-bottom: 1.5rem;
  border: 1px solid #CCC;
  border-spacing: 0;
  border-collapse: collapse; }

table td, table th {
  border: 1px solid #CCC;
  padding: 0.3rem 0.5rem; }

table thead th {
  text-align: left;
  background: #CCC; }

table tr:nth-child(odd) td {
  background: #F1F1F1; }

table tr:hover td {
  background: black;
  color: #FFF;
  border-color: transparent; }

hr {
  margin: 2rem 0; }

header {
  padding: 0;
  /* position: fixed; */
  background: #000;
  right: 0;
  left: 0;
  margin-bottom: 0;
  top: 0;
  z-index: 2; }

.header-container {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;
  position: relative; }

.header-text-container {
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center; }

header .header-logo {
  padding-right: 1em; }

header .header-logo img {
  display: flex; }

nav.main ul {
  padding: 0 0.5rem;
  margin: 0; }

nav.main ul ul {
  display: none;
  position: absolute;
  top: 100%;
  left: -0.5rem;
  width: 10rem; }

nav.main ul ul ul {
  top: -0.5rem;
  right: 100%;
  left: inherit; }

nav.main ul li {
  display: block;
  list-style: none outside;
  position: relative; }

nav.main > ul > li > ul li:not(:last-child) {
  /*padding-bottom: 0.5rem;*/ }

nav.main > ul > li {
  padding: 0 0.5rem 0 0; }

nav.main ul > li > ul li a {
  width: 240px;
  background: rgba(0, 0, 0, 0.6);
  border-top: 1px solid #ffffff;
  display: flex;
  align-items: center;
  padding: 0.94em 0.6em; }

nav.main ul li a {
  padding: 0.94em 0.6em;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  display: flex;
  /*background: #333;
	border-radius: 0.4rem;
	padding: 0.84em 0.4em;
	align-items: center;*/ }

nav.main ul li a:hover,
nav.main ul li:hover > a {
  background: #f47624; }

#main-nav-toggle,
#toggle {
  display: none;
  color: white; }

#toggle {
  display: inline-block;
  align-items: right;
  padding: 1rem;
  font-size: 30px;
  /*position: relative;
	flex: 1 0 100%;	*/ }

#toggle:hover {
  background: #f47624; }

#toggle:before {
  /*display: block;
	content: '';
	position: relative;
	padding-right: 0.3rem;
	font-weight: bold;*/ }

form .search-form {
  position: relative;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  background: white;
  box-sizing: border-box;
  border-radius: 25px;
  border: 4px solid white;
  padding: 5px;
  width: 5%; }

input[type="search"] {
  position: absolute;
  top: 0;
  right: 0;
  width: 20%;
  height: 42.5px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 20px;
  padding: 0 3em 0 1em; }

.fa-search {
  box-sizing: border-box;
  padding: 10px;
  width: 42.5px;
  height: 42.5px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  color: #ffffff;
  text-align: center;
  font-size: 1.2em; }

.search-form:hover {
  cursor: pointer; }

.search-form:hover input {
  display: block; }

.search-form:hover .fa-search {
  background: #f47624;
  color: white; }

@media screen and (max-width: 800px) {
  header {
    position: static; }
  #toggle {
    display: flex; }
  .header-text-container {
    justify-content: space-between; }
  nav.main {
    display: none;
    /*border-top: 2px solid #000;*/ }
  nav.main ul li:not(:first-child) {
    /*padding-top: 0.5rem;*/ }
  #main-nav-toggle:checked + header nav.main {
    display: block;
    flex: 1 0 100%; }
  #main-nav-toggle:checked + header #toggle:before {
    /*content: 'X';*/ }
  .fa-search {
    display: none; }
  .header-container,
  .header-text-container {
    width: 100%; }
  nav.main ul,
  nav.main > ul > li,
  nav.main > ul > li > ul > li {
    padding: 0;
    margin: 0; }
  nav.main ul ul {
    display: block;
    position: static;
    width: 100%; }
  nav.main ul > li > ul li a {
    width: 100%;
    padding-left: 4.5em;
    text-transform: capitalize; }
  nav.main ul > li > ul li a {
    border-top: 1px solid #fe7800; }
  nav.main ul > li > a {
    border-top: 1px solid #fe7800;
    padding-left: 3em;
    text-transform: capitalize;
    /*font-size: 1.5em;*/ } }

@media screen and (min-width: 800px) {
  nav.main ul {
    display: flex; }
  #toggle {
    display: none; }
  nav.main ul li:hover > ul {
    display: block; } }

.footer-content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: white;
  font-weight: 400;
  line-height: 30px;
  padding-top: 1em; }

footer h5 {
  color: #f47624; }

footer ul li {
  list-style: none; }

footer ul li a {
  text-decoration: none;
  color: white;
  text-shadow: 2px 1px #000000; }

footer ul li a:hover {
  color: #f47624;
  text-decoration: underline; }

.footer-content section {
  flex: 0 1 20%;
  padding-bottom: 2em; }

.footer-content section:not(:last-child) {
  padding-right: 2em; }

.social p {
  color: white;
  text-shadow: 2px 1px #000000; }

.social img {
  width: 25px;
  padding-top: 1em; }

@media screen and (max-width: 1000px) {
  .footer-content section {
    flex: 0 0 50%; } }

body {
  /* max-width: 1300px;
	margin: 0 auto; */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0;
  min-height: 100vh; }

body > header:first-of-type {
  background: #000; }

main {
  background: url("../img/johny-goerend-411397-unsplash.jpg") no-repeat center center;
  background-size: cover;
  /* background-attachment: fixed;*/ }

.main-content {
  width: 90%;
  margin: 0 auto; }

.main-content-picture img {
  width: 100%;
  height: auto;
  margin: 0 auto; }

/*main*/
.slogan {
  display: flex;
  justify-content: space-between;
  padding: 1em 0;
  color: white;
  font-weight: bold;
  position: relative; }

.slogan > div > span:first-of-type {
  padding-right: 1em; }

.choose-container2,
.choose-container {
  width: 400px;
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  color: #fff;
  text-align: center;
  line-height: 60px;
  background: #ff7700 url("../img/waves.png") no-repeat center center; }

.choose-container2 {
  display: none; }

.main-content-text {
  background: white;
  padding: 2em 2em 0 2em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 2em; }

.main-content-text section {
  flex: 1 0 20%;
  padding-bottom: 2em; }

.main-content-text section:not(:last-child) {
  padding-right: 2em; }

footer {
  background: url("../img/fenix-footer.jpg") no-repeat center center;
  background-size: cover;
  padding: 1em 3em 3em 3em; }

@media screen and (max-width: 800px) {
  .main-content {
    /* padding: 1em 0; */ }
  .slogan {
    padding: 0.5em 0; }
  .main-content-text section {
    flex: 0 0 50%; } }

@media screen and (max-width: 900px) {
  .choose-container {
    display: none; }
  .choose-container2 {
    display: block;
    max-width: 150px;
    bottom: -10px;
    line-height: 40px;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
    font-weight: bold; }
  .main-content-picture {
    position: relative; } }
